@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search{
  padding-top: 10px;
}
.searchInputs {
  margin-top: 105px;
  display: flex;
}
.card-ctn {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-top: 25px;
}
.search input {
  background-color: white;
  border: 0;
  border-radius: 2px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  width: 300px;
}
@media screen and (min-width: 1366px) and (max-width: 1366px){
  .card-ctn{
      padding-left: 25px;
  }
}
@media screen and (min-width: 768px) and (max-width: 768px){
  .card-ctn{
      padding-left: 25px;
  }
}
@media screen and (max-width: 375px){
  .card-ctn{
      padding-left: 25px;
  }
}

@media screen and (max-width: 320px){
  .card-ctn{
    padding-left: 0px;
}
}
.card-img {
    width: 100px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1 0 21%;
    padding-left: 10px;
    padding-bottom: 25px;
}
.card-img img{
    padding: 10px;
    width: 100px;
    height: 100px;
}
a {
    text-decoration: none;
    color: white;
}


.animCtn{
    margin: auto;
}
* {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
body {
  background: #093145;
  color: white;
}
.App {
  text-align: center;
  background: #093145;
}


.header {
  padding: 10px
}
.search-ctn{
  padding: 10px;
}
.dataCtn{
    display: flex;
    flex-direction: column;
}
.main-detail-ctn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 10px;
}
.civImage{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 50px;
}
.civName{
    padding: 25px;
}
.civDetails{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}
.civDetails li {
    text-align: left;
}
.civDetails h2{
    padding: 5px;
}
.civImage img{
    width: 100px;
    height: 100px;
}
.list-ctn{
    width: 300px;
    align-self: center;
}
.civBonus p{
    text-align: left;
}

.returnBtn{
    padding-top: 5px;
    padding-left: 5px;
    position: fixed;
    top: 0;
    width: 100%;
}
